.main{
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
  
}

.submain{
    
    display: flex;
    width: 95%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.box1{
   
    width: 90%;
    background-color: #fff;
}

.subbox1{
    
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-around;
}

.subbox_{
    display: flex;
    align-items: center;
    width: 70%;
    height: 40px;
}

.quantity{
    margin: .5rem;
    font-size: .9rem;
}

.containerSpinner{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    height: 60vh;
}

.containerArrow{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    margin: 1rem 0 1rem 0;
}

/* .maintitle{
  margin: 0;
} */

.arrowleft{
    margin: 0 1rem 0 5%;
    width: 32px;
    height: 32px;
    color: #282828;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;
}


.containerResumen{
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: flex-start;
    margin: 0 0 0 10%;
}

.box2{
    display: flex;
    flex-direction: column;
    width: 90%;
    background-color: #fff;
}

.subbox2{
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;

}

.subbox2_title{
    width: 70%;
    margin: 0 0 0 1.5rem;
}

.method{
    background-color: #29d884;
    padding: .5rem;
    color: #282828;
    border-radius: 2px;
    text-align: center;
    word-wrap: break-word;
    word-break: break-all;
}

.status{
    margin: 0 0 0 1.5rem;
    color: #29d884;
    border-radius: 2px;
    padding: .3rem;
}


.boxComentarios{
    display: flex;
    align-items: center;
    margin: 0 0 .5rem 1.5rem;
    width: 80%;
   }

.comenario{
    margin: 0 0 0 .5rem;
}