.main{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    transition: 0.3s ;
    height: calc(100vh - 50px);
    background-color: #f5f7f8;
    position: relative;
}

.submain{
    width: 100%;
    height: 100%;
}

.header{
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: #282828 solid 2px;
    
}

.btn{
    height: 70%;
    width: 25%;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border: none;
    margin: 0 2rem 0 0;
    font-weight: 800;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    text-align: center;
    word-wrap: break-word;
    word-break: break-all;
}



.iconplus{
    margin: 0;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    color: #3398db;
    position: relative;
    left: .5rem;
}

.moneyIcon{
    min-width: 30px;
    min-height: 50px;
    margin: 0 1rem 0 .5rem;
    color: #29d884;
}

.containerUsers{
    display: flex; 
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 1rem 0 0 0;
}

.container{
    width: 25%;
    height: 120px;
    margin: 1rem;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 10px;
}

.noUsers{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50vh;
}

.usersIcons{
    min-width: 40px;
    min-height: 40px;
    color: #282828;
}



.containerName{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 40%;
    height: 80%;
   
}

.total{
    font-size: 2rem;
    color: #282828;
}

.name{
    text-transform: capitalize;
    font-size: 1.4rem;
    color: #282828;
}

.iconback{
    width: 30px;
    height: 30px;
    margin: 1rem;
}

.maincontainerInputs{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.containerInput{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem;
}

.input{
    width: 60%;
}

.btncrear{
    width: 50%;
    color: #282828;
    background-color: #3398db;
 
}

@media screen and (max-width: 700px) {
    .btn{
        width: 30%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .total{
        font-size: 1.5rem;
    }

    .name{
        font-size: 1rem;
    }

    .moneyIcon{
        margin: .3rem;
    }

    .container{
        width: 40%;
    }
}